@media (min-width: 769px) and (max-width: 960px) {
  .hero__conteiner {
    padding-left: 0%;
    padding-right: 0%;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 1fr;
    gap: 0;
    height: 100%;
  }
  .hero__conteiner--left, .hero__conteiner--right {
    padding-top: 0%;
  }
  .hero__conteiner--img {
    width: 100vw;
    background-image: url("../../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1234.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 95%;
  }
  .hero__conteiner--right {
    display: grid;
    grid-template-areas: "title title title";
    justify-content: space-evenly;
    align-items: center;
  }
  .hero__conteiner--title {
    grid-area: title;
    height: auto;
    margin-bottom: 0%;
    text-align: center;
  }
  .hero__conteiner--paragraph {
    width: 50vw;
    grid-area: 2/3;
    margin-bottom: 0%;
  }
  .hero__conteiner--btn {
    grid-column: 1/3;
    margin-bottom: 5vh;
  }
}